import React, {useEffect} from 'react'
import PropTypes from 'prop-types'
import Spinner from '../layout/Spinner'
import {connect} from 'react-redux'
import {getGithubRepos} from '../../actions/profile'

const ProfileGithubRepos = ({
	userName,
	getGithubRepos,
	repos
}) => {

	useEffect(() => {
		getGithubRepos(userName)
	}, [getGithubRepos, userName])

	return (
		<div className="profile-github">
			<h2 className="text-primary my-1">Github Repos</h2>
			{repos === null ? <Spinner/> : (
				repos.map(repo => {
					return <div key={repo.id} className='repo bg-white p-1 my-1'>
						<div>
							<h4>
								<a href={repo.html_url} target='_blank' rel='noopener noreferrer'>
									{repo.name}
								</a>
							</h4>
							<p>{repo.description}</p>
						</div>
						<div>
							<ul>
								<li className="badge badge-primary">
									Stars: {repo.stargezers_count}
								</li>
								<li className="badge badge-dark">
									Watchers: {repo.watchers_count}
								</li>
								<li className="badge badge-light">
									Forks: {repo.forks_count}
								</li>
							</ul>
						</div>
					</div>
				})
			)}
		</div>
	)
}

ProfileGithubRepos.propTypes = {
	repos: PropTypes.array.isRequired,
	getGithubRepos: PropTypes.func.isRequired,
	userName: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
	repos: state.profile.repos
})

export default connect(mapStateToProps, {getGithubRepos}) (ProfileGithubRepos)
